import { SyntheticEvent, useState } from "react";
import { Input, Button, PasswordInput } from "@ya.praktikum/react-developer-burger-ui-components";
import cn from "classnames";
import { Link } from "react-router-dom";
import { register } from "../../services/actions/auth";
import { useDispatch } from '../../hooks';
import { Redirect } from 'react-router-dom';

import s from "./register.module.css";

function Register() {
  const [state, setState] = useState({
    name: "",
    email: "",
    password: "",
  });

  const handleInputChange = (event: SyntheticEvent) => {
    const target = event.target as HTMLInputElement;
    const value = target.value;
    const name = target.name;

    setState({
      ...state,
      [name]: value,
    });
  };

  const dispatch = useDispatch();

  const submit = (e: SyntheticEvent) => {
    e.preventDefault();
    dispatch(register(state));
  };
  
  const hasToken = localStorage.getItem('refreshToken')
  
  if (hasToken) {
    return (
      <Redirect
        to={{
          pathname: '/'
        }}
      />
    );
  }
  
  return (
    <section className={cn(s.main, "mt-30")}>
      <form onSubmit={submit} className={s.form}>
        <h1 className="text text_type_main-large mb-6"></h1>
        <Input type={"text"} placeholder={""} onChange={handleInputChange} value={state.name} name={"name"} error={false} errorText={""} size={"default"} />
        <Input type={"email"} placeholder={"E-mail"} onChange={handleInputChange} value={state.email} name={"email"} error={false} errorText={""} size={"default"} />
        <PasswordInput onChange={handleInputChange} value={state.password} name={"password"} />
        <Button type="primary" size="large" >
          
        </Button>
      </form>
      <p className="text text_type_main-default text_color_inactive mt-20">
         ?
        <Link to="/login" type="secondary" className={cn(s.link, "ml-2")}>
          
        </Link>
      </p>
    </section>
  );
}

export default Register;
